@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700,900');

* {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
}

a {
    text-decoration: none;
    color: inherit;
    &:hover {
        text-decoration: none;
        color: inherit;
    }
}

.containerM {
    width: 1164px;
    margin: 0 auto;

    @media (max-width: 1164px) {
        width: auto;
    }
}

.wrap {
    width: 1600px;
    margin: 0 auto;

    @media (max-width: 1164px) {
        width: auto;
    }
}

.clearfix {
    clear: both;
    height: 0;
    width: 0;
}


/* ITEMS_HOVER_ADDTOCART+SHADOW */

.item_hover_wrap {
    position: relative;
    height: 370px;
    display: flex;
    flex-direction: column;
    color: #222222;
    &:hover {
        box-shadow: 0 5px 8px rgba(0, 0, 0, 0.16);
    }
    
    .item_hover_addToCart {
        position: absolute;
        background-color: rgba(58, 56, 56, 0.83);
        height: 280px;
        width: 261px;
        display: none;

        .addToCart_tablet {
            display: block;
            text-decoration: none;
            color: #ffffff;
            margin-top: 100px;
            margin-left: 70px;
            width: 125px;
            line-height: 40px;
            text-align: center;
            color: #ffffff;
            font-size: 13px;
            font-weight: 700;
            border: 1px solid white;
            border-radius: 3px;
        }
    }

    .item_hover_shadow {
        position: absolute;
        top: 280px;
        height: 85px;
        width: 261px;
        box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.16);
        background: white;
        z-index: -1;
        display: none;
    }
}



.item_hover_wrap:hover .item_hover_addToCart,
.item_hover_wrap:hover .item_hover_shadow {
    display: block;
}

/* /ITEMS_HOVER_ADDTOCART+SHADOW */


/* HEADER */

header {
    height: 100px;
    border-bottom: 1px solid #ececec;

    @media (max-width: 730px) {
        height: auto;
    }

    .headerInside {
        height: inherit;
        display: flex;
        flex-direction: row;
        align-items: center;

        @media (max-width: 730px) {
            flex-direction: column;
            height: auto;
        }

        .logo {
            line-height: 51px;
            width: 165px;
            background: url(../imgs/logo.png) no-repeat;
            color: #222222;
            font-size: 27px;
            font-weight: 300;
            box-sizing: border-box;
            padding-left: 72px;
            margin-right: 50px;

            @media (max-width: 730px) {
                padding-bottom: 15px;
                margin-right: 0;
            }

            span {
                color: #f16d7f;
                font-weight: 400;
            }
        }
    }
}

/* SEARCH */

.headerInside .searchWrap {
    display: flex;
    align-items: center;
    height: 40px;
    overflow: hidden;
    /*чтобы не торчали уголки блоков с скруглёнными границами*/
    border: 1px solid #e6e6e6;
    border-radius: 3px;

    .searchBrowse {
        height: inherit;
        width: 90px;
        box-sizing: border-box;
        padding: 11px;
        padding-right: 0;
        font-size: 14px;
        font-weight: 300;
        border-right: 1px solid #e6e6e6;
        background-color: #f9f9f9;

        a {
            color: #222222;
        }
    }

    input {
        height: inherit;
        box-sizing: border-box;
        padding-left: 16px;
        border: none;
    }

    button {
        height: inherit;
        background: inherit;
        width: 36px;
        border: none;
        border-left: 1px solid #e6e6e6;
    }
}

/* /SEARCH */

.headerInside .buttons {
    margin-left: auto;
    display: flex;

    @media (max-width: 730px) {
        margin-top: 15px;
        margin-left: 0;
    }
}

.headerInside .cartLogo {
    /*margin-left: auto;*/
    margin-right: 25px;
    align-self: center;
}

.headerInside .myAccount {
    background-color: #f16d7f;
    padding: 15px;
    color: #ffffff;
    font-size: 15px;
    font-weight: 400;
    border: none;
    border-radius: 3px;
}

/* /HEADER */

/* TOP_NAVIGATION */

.topNav {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 730px) {
        flex-direction: column;
        height: auto;
        padding: 15px 0;
    }

    a {
        display: block;
        text-decoration: none;
        color: #222222;
        font-size: 14px;
        font-weight: 700;
        margin-right: 30px;
        padding: 0 10px;

        @media (max-width: 730px) {
            margin: 0;
            padding-top: 5px;
        }

        &:hover {
            border-bottom: 3px solid #ef5b70;
            color: #f16d7f;
        }
    }
}

/* /TOP_NAVIGATION */

/* NEW_ARRIVALS */

.newArrivalsWrap {
    background-color: #f8f3f4;
    width: 1600px;
    height: 150px;

    @media (max-width: 1164px) {
        width: auto;
    }

    .newArrivals {
        display: flex;
        justify-content: space-between;
        height: inherit;
        align-items: center;

        @media (max-width: 520px) {
            flex-direction: column;
            justify-content: center;
        }

        h1 {
            color: #f16d7f;
            font-size: 24px;
            font-weight: 400;
        }

        h2 {
            display: inline-block;
            font-size: 14px;
            color: #636363;
            font-weight: 300;

            &:hover {
                color: #f16d7f;
                font-weight: 700;
            }
        }
    }
}

/* /NEW_ARRIVALS */


/* INDEX_MAIN */

/* TOP_BANNER */

.topBannerWrap {
    height: 610px;
    width: 1600px;
    background: #e8e8e8;
}

.topBanner {
    background: url(../imgs/mainPageTopBanner.png) -180px;
    height: inherit;
    padding-top: 225px;
    box-sizing: border-box;
}

.topBanner .topBannerRectangle {
    height: 92px;
    width: 12px;
    background: url(../imgs/mainPageTopBannerRedRectangle.png);
    float: left;
}

.topBanner .topBannerText p:first-child {
    color: #222;
    line-height: 45px;
    font-size: 60px;
    letter-spacing: 1.5px;
}

.topBanner .topBannerText span:first-child {
    line-height: 65px;
    font-size: 40px;
    font-weight: 400;
    letter-spacing: 1px;
}

.topBanner .topBannerText span:last-child {
    color: #f16d7f;
    letter-spacing: 1px;
    font-size: 40px;
    font-weight: 400;
}

/* /TOP_BANNER */

/* FIRSTSECTION */

.firstSection {
    margin-top: 100px;
    box-sizing: border-box;
    height: 920px;
    display: flex;
    flex-wrap: wrap;
}

.firstSection div {
    margin-right: 20px;
    margin-bottom: 20px;
    position: relative;
}

.firstSection div:last-child {
    position: relative;
    bottom: 281px;
}

.firstSection .firstSectionPics p {
    position: absolute;
    top: 45px;
    background: white;
    padding: 10px 15px;
    box-sizing: border-box;
    color: #222222;
    font-size: 18px;
    font-weight: 900;
}

.firstSection div p span {
    color: #f16d7f;
    font-size: 26px;
    font-weight: 900;
}

/* /FIRSTSECTION */

/* FEATURED_ITEMS */

.featuredItems {
    h1 {
        color: #222222;
        font-size: 24px;
        font-weight: 700;
        margin-bottom: 0;
        text-align: center;
        letter-spacing: 0.75px;
    }

    h2 {
        margin-top: 10px;
        color: #9f9f9f;
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 0.35px;
        text-align: center;
    }

    .featuredItemsPics {
        display: flex;
        flex-wrap: wrap;
        margin-top: 20px;

        .featuredItemsPic {
            margin-right: 30px;
            margin-top: 40px;
            height: 370px;
            display: flex;
            flex-direction: column;
            color: #222222;

            p {
                font-size: 13px;
                font-weight: 400;
                text-transform: uppercase;
                margin-top: 20px;
                margin-left: 15px;

                span {
                    display: block;
                    margin-top: 15px;
                    color: #f16d7f;
                    font-size: 16px;
                    font-weight: 700;
                }
            }
        }
    }
}



.featuredItems h3 {
    font-size: 16px;
    font-weight: 700;
    line-height: 54px;
    height: 50px;
    width: 210px;
    background: #f16d7f;
    margin: 0 auto;
    text-align: center;

    a {
        color: #ffffff;
    }
}

/* /FEATURED_ITEMS */

/* BOTTOM_BANNER */

.bottomBanner {
    height: 740px;
    position: relative;
    margin-top: 115px;
    box-sizing: border-box;
}

.bottomBanner .bottomBannerHeader {
    position: absolute;
    top: 145px;
    left: 40px;
    color: #ffffff;
    font-size: 54px;
    font-weight: 700;
    letter-spacing: 1.5px;
}

.bottomBanner .bottomBannerHeader span {
    color: #f16d7f;
    letter-spacing: 1.5px;
}

.bottomBanner .bottomBannerHeader p {
    font-size: 32px;
    letter-spacing: 0.9px;
}

.bottomBanner .merits .delivery .truckPic {
    height: 40px;
    width: 47px;
    background: url(../imgs/mainPage_BottomBanner_truckPic.png) no-repeat;
}

.bottomBanner .merits .sales .salesPic {
    height: 40px;
    width: 47px;
    background: url(../imgs/mainPage_bottomBanner_salesPic.png) no-repeat;
}

.bottomBanner .merits .qualityAssurance .qualityAssurancePic {
    height: 40px;
    width: 47px;
    background: url(../imgs/mainPage_bottomBanner_crownPic.png);
}

.bottomBanner .merits {
    position: absolute;
    top: 30px;
    right: 40px;
    width: 340px;
}

.bottomBanner .merits .merit {
    display: flex;
    flex-direction: row;
}

.bottomBanner .merits .merit:not(:first-child) {
    margin-top: 30px;
}

.bottomBanner .merits .bottomBanner_meritsText {
    margin-left: 30px;
    width: 220px;
}

.bottomBanner .merits .bottomBanner_meritsText p {
    margin-top: 15px;
    width: 220px;
    color: #fbfbfb;
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
    letter-spacing: 0.35px;
}

.bottomBanner .merits .bottomBanner_meritsText h4 {
