/* SHOPPING_CART_MAIN */

/* CART */

/* CART_TOP */

.cart {
    margin-top: 90px;
}

.cart h4 {
    float: left;
    color: #222222;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: -0.2px;
}

.cart .headers {
    display: flex;
    justify-content: space-between;
    width: 630px;
    float: right;
    color: #222222;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: -0.2px;
    margin-bottom: 20px;
}

/* /CART_TOP */

/* CART_DETAILS */

.cart .products .product {
    height: 120px;
    margin: 20px 0;
}

.cart .products .productPic {
    display: flex;
    float: left;
}

.cart .products .productPic div {
    margin-left: 20px;
    padding-top: 15px;
}

.cart .products .productPic div p {
    margin-top: 40px;
    font-size: 13px;
    line-height: 20px;
    color: #575757;
    font-weight: 300;
}

.cart .products .productPic div h6 {
    color: #222222;
    font-size: 13px;
    font-weight: 400;
}

.cart .products .details {
    display: flex;
    justify-content: space-between;
    float: right;
    padding-left: 25px;
