    color: #4a4a4a;
    font-size: 14px;
    font-weight: 700;
    text-align: center;
}

.cart_middleButtons a:first-child {
    float: left;
}

.cart_middleButtons a:last-child {
    float: right;
}

/* /CART_MIDDLE_BUTTONS */

/* CART_BOTTOM_SECTIONS*/

.cart_bottomSections {
    margin-top: 60px;
    display: flex;
}

.cart_bottomSections section:not(:last-child) {
    margin-right: 35px;
}

.cart_bottomSections legend {
    margin-bottom: 25px;
}

.cart_bottomSections form input {
    display: block;
    margin-top: 25px;
    width: 355px;
    height: 45px;
    border: 1px solid #eaeaea;
    padding-left: 20px;
