    font-weight: 300;
}

.cart_bottomSections form fieldset {
    border: none;
}

.cart_bottomSections form button {
    width: 100px;
    height: 35px;
    border: 1px solid #eaeaea;
    background-color: #ffffff;
    color: #4a4a4a;
    font-size: 11px;
    font-weight: 700;
    margin-top: 25px;
}

.cart_bottomSections .total_sumWrap {
    width: 360px;
    height: 214px;
    background-color: #f5f3f3;
