    flex-direction: column;
    align-items: flex-end;

}

.cart_bottomSections .total_sum h3 {
    color: #4a4a4a;
    font-size: 11px;
    font-weight: 400;
}

.cart_bottomSections .total_sum h4 {
    color: #222222;
    font-size: 16px;
    font-weight: 700;
}

.cart_bottomSections .total_sum h4 span {
    color: #f16d7f;
}

.cart_bottomSections .total_sum h4 {
    margin-top: 15px;
}

.cart_bottomSections .total_sum .horizontal_line {
    margin-top: 20px;
    width: 275px;
    height: 1px;
    background-color: #e2e2e2;
}

.cart_bottomSections .total_sum a {
    width: 273px;
    line-height: 50px;
    background-color: #f16d7f;
    color: #ffffff;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    margin-top: 15px;
}

/* /CART_BOTTOM_SECTIONS*/

