/* CHECKOUT-MAIN */

.checkoutWrap {
    height: auto;
}

.checkout {
    padding-top: 90px;
}

.checkoutWrap .shippingAddress_Flex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.checkout summary {
    color: #222222;
    font-size: 16px;
    font-weight: 700;
    line-height: 70px;
    border-top: 1px solid #e8e8e8;
}

.shippingAddress summary:first-child {
    border: none;
}

/* TO_REGISTRATION */

.checkoutWrap .to_registration {
    margin-left: 35px;
}

@media (max-width: 730px) {
    .checkoutWrap .to_registration {
        padding-bottom: 40px;
        border-bottom: 1px solid gray;
    }
}

.checkoutWrap .to_registration h2 {
    color: #222222;
    font-size: 13px;
    font-weight: 400;
    margin-top: 40px;
}

.checkoutWrap .to_registration h3 {
    color: #b3b2b2;
    font-size: 13px;
    font-weight: 400;
    margin-top: 15px;
    margin-bottom: 25px;
}

.checkoutWrap .to_registration span {
    line-height: 35px;
}

.checkout input[name="checkout_or_reg"] {
    display: none;
}

.checkout label[for="guest"],
.checkout label[for="reg"] {
    background: url(../imgs/input_radio_empty.png) no-repeat 3px 3px;
    padding-left: 20px;
}

.checkout #guest:checked + label,
.checkout #reg:checked + label {
    background: url(../imgs/input_radio_checked.png) no-repeat 3px 3px;
}

.checkoutWrap .to_registration p {
    color: #b3b2b2;
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    height: 70px;
}

.checkoutWrap .to_registration button {
    width: 180px;
    height: 50px;
    border: 1px solid #eaeaea;
    background-color: #ffffff;
    color: #4a4a4a;
    font-size: 14px;
    font-weight: 700;
}

