/* SINGLE_PAGE_OFFER */

.singlePageOffer {
    /*height: 730px;*/
    max-width: 640px;
    min-height: 730px;
    margin-top: -75px;
    background-color: white;
    border: 1px solid #eaeaea;
    display: flex;

    .singlePageOfferContent {
        min-height: 600px;
        /*height: 600px;*/
        /*width: 540px;*/
        max-width: 540px;
        align-self: center;
        display: flex;
        flex-direction: column;

        .text_info {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;

            h3 {
                color: #f16d7f;
                font-size: 14px;
                font-weight: 700;
            }

            .redRectangle {
                width: 63px;
                height: 3px;
                background-color: #ef5b70;
                margin: 15px 0;
            }

            h2 {
                color: #212121;
                font-size: 18px;
                font-weight: 400;
                font-family: "Lato - Black", sans-serif;
                margin-bottom: 65px;
            }

            .description {
                color: #5e5e5e;
                font-size: 14px;
                font-weight: 300;
                margin-bottom: 25px;
            }

            p {
                color: #2f2f2f;
                font-size: 14px;
                font-weight: 700;


                .gray {
                    color: #b9b9b9;

                    &:last-child {
                        margin-left: 45px;
                    }
                }
            }

            h1 {
                color: #ef5b70;
                font-size: 24px;
                font-weight: 700;
                margin: 45px 0;
            }

            .horizontalLine {
                width: 600px;
                height: 1px;
                border: 1px solid #eaeaea;
                background-color: #ef5b70;

                @media (max-width: 600px) {
                    width: 320px;
                }

                @media (max-width: 320px) {
                    width: 180px;
                }
            }
        }

        .addToCart {
            margin-top: 60px;

            .inputs {
                display: flex;
                justify-content: space-between;
                margin-bottom: 30px;

                @media (max-width: 600px) {
                    flex-direction: column;
                    align-items: center;
                }


                div {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    @media (max-width: 600px) {
                        width: 80%;
                    }

                    label {
                        color: #2f2f2f;
                        font-size: 14px;
                        font-weight: 700;
                        line-height: 24px;
                    }

                    input {
                        width: 144px;
                        height: 35px;
                        border: 1px solid #eaeaea;
                        padding-left: 15px;
                        color: #bcbcbc;
                        font-size: 13px;
                        font-weight: 400;

                        @media (max-width: 600px) {
                            width: 80%;
                        }
                    }
                }

                /*.color if {
                    background: url(../imgs/) no-repeat 10px;
                }*/
            }

            .addToCartButton {
                display: block;
                width: 540px;
                line-height: 55px;
                border: 1px solid #ef5b70;
                background-color: #ffffff;
                background: url(../imgs/red_cart2.png) no-repeat center;
                text-align: center;
                color: #ef5b70;
                font-size: 16px;
                font-weight: 700;

                @media (max-width: 600px) {
                    width: auto;
                }
            }
        }
    }
}

