/* U_MAY_LIKE_ALSO */

.uMayLikeAlso {
    min-height: 650px;
    padding-top: 120px;
    box-sizing: border-box;

    h1 {
        text-align: center;
        color: #4d4d4d;
        font-family: "Lato - Black";
        font-size: 24px;
        font-weight: 400;
        margin-bottom: 70px;
    }

    .block {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        
        @media (max-width: 842px) {
            justify-content: space-around;
        }

        section {
            margin: 15px;
        }

        h2 {
            margin-top: 20px;
            color: #6f6e6e;
            font-size: 14px;
            font-weight: 400;
            margin-left: 10px;
        }

        p {
            color: #f16d7f;
            font-size: 16px;
            font-weight: 700;
            margin-top: 15px;
            margin-left: 10px;
        }
    }
}




/* /SINGLE_PAGE-MAIN */


/* FEEDBACK */

.feedbackWrap {
    margin-top: 80px;
    width: 1600px;
    background: url(../imgs/mainPage_feedbackBackgroundPic.png) #ccc;
    background-blend-mode: overlay;

    @media (max-width: 1164px) {
        width: auto;
        height: auto;
    }

    .feedback {
        display: flex;

        @media (max-width: 930px) {
            height: auto;
            flex-direction: column;
        }
    }
}

/* COMMENTS_SECTION */

.feedback .commentsSection {
    max-width: 560px;
    height: 440px;
    box-sizing: border-box;
    margin-left: 0;
    border-right: 1px solid white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex: 1 1;

    @media (max-width: 930px) {
        border: none;
    }

    @media (max-width: 930px) {
        height: 240px;
        margin-top: 20px;
    }

    .commentatorPic1 {
        height: 83px;
        width: 83px;
        background: url(../imgs/mainPage_feedback_commentatorPic1.png);
        float: left;
    }

    .commentText {
        width: 386px;
        color: #222224;
        font-size: 16px;
        font-style: italic;
        line-height: 26px;
        letter-spacing: 0.4px;

        @media (max-width: 501px) {
            height: auto;
            width: auto;
        }
    }

    .commentatorData {
        color: #f16d7f;
        font-size: 16px;
        font-weight: 700;
        letter-spacing: 0.4px;

        span {
            color: #222224;
            font-size: 12px;
            font-weight: 300;
            letter-spacing: 0.3px;
        }
    }

    .commentSwitch {
        img {
            &:not(:first-child) {
                margin-left: 10px;
            }
        }
    }
}

/* /COMMENTS_SECTION */

/* SUBSCRIPTION_SECTION */

.subscription_section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex: 1 1;

    @media (max-width: 930px) {
        max-width: 560px;
        height: 180px;
    }
}


.subscription_section .subscriptionSection_header {
    p {
        color: #222224;
        font-weight: 400;
        font-size: 24px;
        letter-spacing: 0.6px;
    }

    span {
        font-size: 14px;
        font-weight: 400;
    }
}

.subscription_section {
    .subscribe input[type="text"] {
        border: none;
        height: 46px;
        padding-left: 30px;
        width: 260px;
        opacity: 0.67;
        color: #222224;
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 0.35px;
        outline: none;
        background-color: #e1e1e1;

        @media (max-width: 544px) {
            width: inherit;
        }
    }

    .subscribe button {
        border: none;
        height: 46px;
        padding-left: 0px;
        width: 104px;
        border-radius: 0 23px 23px 0;
        background-color: #f16d7f;
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 0.35px;
    }

    .subscribe {
        margin-top: 35px;
        height: auto;
        border-radius: 23px;
        background-color: #e1e1e1;
        overflow: hidden;
    }
}

/* /SUBSCRIPTION_SECTION */

