/* FOOTER */

.footer {
    width: 1600px;

    @media (max-width: 1164px) {
        width: auto;
    }
}

/* FOOTER_INSIDE */

.footerInside {
    margin-top: 110px;
    margin-bottom: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .logo {
        line-height: 51px;
        width: 165px;
        background: url(../imgs/logo.png) no-repeat;
        color: #222222;
        font-size: 27px;
        font-weight: 300;
        padding-left: 72px;

        span {
            color: #f16d7f;
            font-weight: 400;
        }
    }

    .footerSection {
        margin-left: 15px;

        p {
            width: 427px;
            color: #898989;
            font-size: 14px;
            font-weight: 300;
            line-height: 24px;
            letter-spacing: 0.35px;
            margin-top: 30px;

            @media (max-width: 500px) {
                width: auto;
            }
        }

        .footerSection_header {
            font-size: 18px;
            font-weight: 700;
            letter-spacing: 0.45px;
            margin-top: 12px;

            a {
                text-decoration: none;
                color: #f16d7f;
            }
        }

        ul li {
            list-style: none;
            margin-top: 30px;

            &:first-child {
                margin-top: 52px;
            }

            a {
                text-decoration: none;
                color: #898989;
                font-size: 16px;
                font-weight: 400;
                letter-spacing: 0.4px;
            }
        }
    }
}

