.footer .footerBottomWrap {
    height: 79px;
    width: 1600px;
    background-color: #e6e6e6;
    padding-top: 25px;
    box-sizing: border-box;

    @media (max-width: 1164px) {
        width: auto;
    }

    .footerBottom {
        span {
            width: 260px;
            color: #7a7a7a;
            font-size: 16px;
            font-weight: 400;
            letter-spacing: 0.4px;
        }

        .socialLogos {
            display: flex;
            float: right;
            margin-right: 21px;
        }

        .socialLogo {
            height: 32px;
            width: 32px;
            margin-left: 8px;
            color: #000000;
            font-size: 32px;

            &:hover {
                color: red;
            }
        }
    }
}

